.home-content {
    display: flex;
    flex-direction: column;
    width: 70%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    /* grid-template-rows: 400px 200px 200px 1fr; */
    grid-template-rows: 400px 200px 60px 1fr;
    gap: 15px;
    grid-template-areas: 
        "major major major"
        /* "minor minor minor" */
        "cetus fortuna deimos"
        "explore-title explore-title explore-title"
        "primary secondary warframe";
}

.news {
    background: var(--sec-bg-color)
}

.major-news {
    grid-area: major;
    width: 100%;
    height: 400px;
}

.minor-news {
    grid-area: minor;
    width: 100%;
    height: 100%;
}

.cetus {
    grid-area: cetus;
    border: 2px solid rgb(0, 255, 0);
    border-radius: 10px;
}

.fortuna {
    grid-area: fortuna;
    border: 2px solid rgb(0, 204, 255);
    border-radius: 10px;
}

.deimos {
    grid-area: deimos;
    border: 2px solid rgb(255, 0, 0);
    border-radius: 10px;
}

.img-link {
    min-height: 100px;
    border: 2px solid #ddd;
}

.explore-title {
    grid-area: explore-title;
    font-family: 'Orbitron', sans-serif;
    font-size: 1.8rem;
}