.content-wrapper {
  font-family: 'Ubuntu', sans-serif;
  margin: 5rem 0 3rem 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.item-grid {
  display: grid;
  width: 65%;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  justify-items: center;
  align-items: center;
  gap: 18px;
}
